import * as React from 'react';
import {
    AppBar,
    Backdrop,
    Box, CardMedia, Drawer, IconButton, ImageListItemBar, linkClasses, Toolbar, Typography
} from '@mui/material';
import { Stack } from '@mui/system';
import { useDispatch, useSelector } from 'react-redux';
import CloseRoundedIcon from '@mui/icons-material/CloseRounded';
import CloseFullscreenRoundedIcon from '@mui/icons-material/CloseFullscreenRounded';
import FileDownloadRoundedIcon from '@mui/icons-material/FileDownloadRounded';
import NavigateNextRoundedIcon from '@mui/icons-material/NavigateNextRounded';
import NavigateBeforeRoundedIcon from '@mui/icons-material/NavigateBeforeRounded';
import OpenInFullRoundedIcon from '@mui/icons-material/OpenInFullRounded';
import { viewImage } from '../../../../../../redux/apps/medialibrary/medialibraryNavRight';
import getFileExtension from '../../../../../../services/getFileExtension'
import useSize from '../../../../../../services/useSize';

export default function PhtoLibraryViewer () {
    const image = useSelector(state => state.medialibraryNavRight
        .value.photoLibrary.selectedImage
    );
    const [open, setOpen] = React.useState(!!image);
    const images = useSelector(state => state.medialibraryNavRight.value.data.images);
    //const imageIndex = images?.findIndex(({_id}) => _id === image?._id);
    const dispatch = useDispatch();
    const [fullScreen, setFullScreen] = React.useState(false);
    const size = useSize();
    const isSmallScreen = !!['xs', 'md'].find( key  => size.key === key);

    const options = [
        {
            title: 'Fermer',
            icon: CloseRoundedIcon,
            action() {
                setOpen(false);
            }
        },
        {
            title: fullScreen ? 'Reduire' : 'Agrandir',
            icon: fullScreen ? CloseFullscreenRoundedIcon : OpenInFullRoundedIcon,
            action() {
                setFullScreen(!fullScreen);
            },
            fullScreen,
        },
        // {
        //     title: 'Image précédente',
        //     disabled: imageIndex === 0,
        //     icon: NavigateBeforeRoundedIcon,
        //     action() {
        //         dispatch(viewImage({image: 
        //             images[imageIndex - 1]
        //             }));
        //     }
        // },
        // {
        //     title: 'Image suivate',
        //     icon: NavigateNextRoundedIcon,
        //     disabled: imageIndex === images.length - 1,
        //     action() {
        //         dispatch(viewImage({image: 
        //         images[imageIndex + 1]
        //         }));
        //     }
        // },

    ];

    React.useEffect(() => {
        if(image) 
            setOpen(true)
    }, [image, setOpen]);

    return (
        <Box
            {...fullScreen ? {
                component: Backdrop,
                open,
                zIndex: theme => theme.zIndex.drawer + 1,
                sx: {
                    bgcolor: theme => theme.palette.background.paper + 'dd',
                    flexDirection: 'column',
                    backdropFilter: 'blur(15px)',
                    '& > div': {
                       alignItems: 'center',
                       width: window.innerWidth,
                    }
                }
            } : {
                ...isSmallScreen ? {
                    component: Drawer,
                    variant: 'persistent',
                    open,
                } : {
                    component: 'div',
                    sx: {
                        display: 'flex', 
                        flex: open ? .5 : 0,
                        bgcolor: 'background.paper',
                        overflow: 'hidden',
                        transition: theme => theme.transitions.create('flex', {
                            easing: theme.transitions.easing.sharp,
                            duration: theme.transitions.duration.leavingScreen,
                        }),
                        position: 'relative',
                    }, 
                },
                onTransitionEnd() {
                    if(!open) 
                        dispatch(viewImage({image: null}));

                },


            }}  
            
        >
            <Stack
                display="flex"
                flex={1}
                component="div"
                overflow="auto"
                position="relative"
            >
                
                <Box
                    component={AppBar}
                    sx={{
                        top: 0, 
                        p:0,
                        m:0,
                        display: 'flex',
                        position: 'absolute',
                        zIndex: 1000,
                        boxShadow: 0,
                        bgcolor: 'transparent',
                        ...fullScreen ? {} : {
                            background:
                            'linear-gradient(to bottom, rgba(0,0,0,0.7) 0%, ' +
                            'rgba(0,0,0,0.3) 70%, rgba(0,0,0,0) 100%)',
                        }
                    }}
                >
                    <Toolbar 
                        sx={{
                            p:0,
                            m: 0,
                            mt: fullScreen ? 0 : - 1
                        }}
                    >
                        {
                            options.map((btn, index) => 
                            index ? (fullScreen ? btn.fullScreen : true)  &&
                            (<IconButton 
                                key={index} 
                                onClick={btn.action}
                                disabled={btn.disabled}
                                size="small"
                                color={fullScreen ? 'default' : 'inherit'}
                            >
                                <btn.icon
                                    fontSize="small"
                                />
                            </IconButton>): (fullScreen ? btn.fullScreen : true) &&
                            <Box sx={{flexGrow: 1}} key={index} >
                                <IconButton 
                                    onClick={btn.action}
                                    disabled={btn.disabled}
                                    size="small"
                                    color={fullScreen ? 'default' : 'inherit'}
                                >
                                    <btn.icon
                                        fontSize="small"
                                    />
                                </IconButton>
                            </Box>
                            )
                        }
                    </Toolbar>
                </Box>
                <Box 
                    sx={{
                        display: 'flex', 
                        justifyContent: 'center',
                        alignItems: 'center',
                        ...fullScreen ? {flex: 1} : {}
                    }}
                >
                    <CardMedia
                        src={image?.contentUrl}
                        component="img"
                        srcSet={image?.contentUrl}
                        sx={{ ...fullScreen ? {height: 400} : {} }}
                    />
                    
                </Box>
                <Box sx={{mb: 3, mx: 1, textOverflow: 'ellipsis', overflow: 'hidden'}}>
                    <Typography color='text.primary' mt={1} variant="body1" >{image?.title}</Typography>
                    <Typography color='text.secondary' variant="caption">{image?.title}</Typography>
                </Box>
            </Stack>
        </Box>
    );

}