import { 
    Box,
    Divider,
    Stack
} from '@mui/material';
import { useDispatch, useSelector } from 'react-redux';
import GroupButtonTab from '../../../../../../components/GroupButtonTab';
import { selectedTypeBook } from '../../../../../../redux/apps/medialibrary/medialibraryNavRight';

export default function BookLibraryHeader () {
    const dispatch = useDispatch();
    const types = useSelector(state => 
        state.medialibraryNavRight.value.bookLibrary.bookTypes
    );
     const selectedBookType = useSelector(state => 
             state.medialibraryNavRight.value.bookLibrary.selectedBookType
    );
 
    const options = [
        {
            label: 'TOUS',
            value: null,
        },
        ...types?.map(label => ({label, value: label})) || [],
    ];
    const defaultIndex = options.findIndex(({value}) => value === selectedBookType);
  
    const handleChange = (event, tab) => {
        const { value } = tab
        dispatch(selectedTypeBook({type:value || null}));
    };

    return (
        <Box
            sx={{
                m:1, 
                mt: 0,
                bgcolor: 'background.paper',
                top: 0,
                position: 'sticky',
                zIndex: theme => theme.zIndex.appBar - 1,
            }}
        >
          <Stack 
            mt={1}
          >
            <GroupButtonTab
                options={options}
                indicator={false}
                onChange={handleChange}
                defaultIndex={defaultIndex}
            />
          </Stack>
          <Divider/>
        </Box>
    )
}