import {
    Box, 
    Grid,
} from '@mui/material';
import { useSelector } from 'react-redux';
import VideoPlan from './VideoPlan';


export default function BookLibraryContent () {
    const films = useSelector(state => state.medialibraryNavRight.value.data.films);
    const isMall = useSelector(state => state.medialibraryNavLeft.value.open);

    return (
        <Box
            sx={{
                display: 'flex',
                flex: 1,
                px: 1,
                mt: 1,
            }}
        >
            <Box>
                <Grid 
                    container 
                    spacing={1} 
                    sx={{pb: 0}}
                >
                        {films?.map((film, index) => (
                            <Grid
                                item
                                xs={12}
                                md={6}
                                lg={isMall ? 4 : 3}
                                xl={isMall ? 3 : 2}
                                key={index}
                            >
                                <VideoPlan
                                    src={film.contentUrl}
                                    title={film.title}
                                    description={film.description}
                                    //date={handleLocalDateStr(film.createdAt)}
                                />
                            </Grid>
                        ))}
                </Grid>
            </Box>
        </Box>
    );

}