import * as React from 'react';
import ReactDOM from 'react-dom';
import { 
    ClickAwayListener,
    Grow,
    Paper,
    Popper,
    MenuItem,
    MenuList,
    ListItemText,
    Tabs,
    Box,
    Chip,
    IconButton,
 } from "@mui/material"
import ArrowDropDownCircleRoundedIcon from '@mui/icons-material/ArrowDropDownCircleRounded';

const PopperWrapper = ({children}) => ReactDOM.createPortal(
  children, 
  document.getElementById('root')
);


export function SplitButton({options, selectd, onClick, defaultValue, onChange}) {

  const [open, setOpen] = React.useState();
  const anchorRef = React.useRef(null);
  const [selectedValue, setSelectedValue] = React.useState(
    defaultValue || options[0]?.value
  );
  const option = options.find(({value}) => selectedValue === value);

  const handleMenuItemClick = (event, option) => {
    setSelectedValue(option.value);
    setOpen(false);
    if(typeof onClick === 'function')
      onClick(event, option);
    if(typeof onChange === 'function')
      if(selectedValue !== option.value) 
        onChange(event, option);

  };

  const handleToggle = () => {
    setOpen((prevOpen) => !prevOpen);
  };

  const handleClose = (event) => {
    if (anchorRef.current && anchorRef.current.contains(event.target)) {
      return;
    }
    setOpen(false);
  };

  return (
    <React.Fragment>
      <Chip
        variant={selectd ? 'contained' : 'outlined'} 
        color="primary"
        onClick={event => {
          handleMenuItemClick(event, option)
        }}
        label={option?.label}
        onDelete={options.length  > 1 ? handleToggle : null}
        deleteIcon={ 
          <IconButton
              size="small"
              sx={{m:0, p: 0}}
              aria-controls={open ? 'split-button-menu' : undefined}
              aria-expanded={open ? 'true' : undefined}
              aria-label="select merge strategy"
              aria-haspopup="menu"
              ref={anchorRef}
              //onClick={handleToggle}
              onMouseEnter={handleToggle}
              onMouseLeave={handleToggle}
          >
              <ArrowDropDownCircleRoundedIcon/>
          </IconButton>
        }
      />

      {options.length  > 1 &&
      <PopperWrapper>
        <Popper
          sx={{
            zIndex: 1,
          }}
          open={!!open}
          anchorEl={anchorRef.current}
          role={undefined}
          onMouseEnter={handleToggle}
          onMouseLeave={handleClose}
          transition
          disablePortal
          //placement="bottom-start"
        >
          {({ TransitionProps, placement }) => (
            <Grow
              {...TransitionProps}
              style={{
                transformOrigin:
                  placement === 'bottom' ? 'center top' : 'center bottom',
              }}
            >
              <Paper>
                <ClickAwayListener onClickAway={handleClose}>
                  <MenuList id="split-button-menu" autoFocusItem>
                    {options.map((opt, index) => (
                      <MenuItem
                        key={index}
                        selected={opt.value === option.value}
                        onClick={(event) => handleMenuItemClick(event, opt)}
                      >
                        <ListItemText
                          primaryTypographyProps={{
                            sx: {
                              color: 'text.primary',
                              fontSize: 12,
                            }
                          }}
                        >{opt.label}</ListItemText>
                      </MenuItem>
                    ))}
                  </MenuList>
                </ClickAwayListener>
              </Paper>
            </Grow>
          )}
        </Popper>
      </PopperWrapper>
      }
    </React.Fragment>
  );
};

const CustomTab = props => {
  const { options, selectd, value, onChange } = props;

  const handleChange = (event, option) => {
    onChange(event, {
      selectedIndex: value,
      option,
    })
  }

  return (
      <Box
        sx={{m:.5}}
      >

        <SplitButton
            options={options}
            selectd={selectd}
            onClick={handleChange}
        />
      </Box>
  )
};

export default function GroupButtonTab ({defaultIndex, onChange, options, indicator,...otherProps}) {
    const defaultOption = options[ defaultIndex || 0 ];
    const [values, setValues] = React.useState({
      index: defaultIndex || 0,
      option: {
        label: defaultOption?.label,
        value: defaultOption?.value || defaultIndex || 0,
    }});

    const handleChange = (event, {selectedIndex: index, option}) => {
      if(values.index !== index) {
        setValues({index, option});
        if(typeof onChange === 'function')
          onChange(event, option);
      } else {
        if(JSON.stringify(values.option) !== JSON.stringify(option)) {
          setValues({index, option});
          if(typeof onChange === 'function')
            onChange(event, option);
        }
      }
    }

    return (
        <Tabs
          value={values.index}
          onChange={handleChange}
          variant="scrollable"
          TabScrollButtonProps={{
            sx: {
              height: '100%',
            }
          }}
          {...otherProps}
          TabIndicatorProps={{
            ...indicator ? {

            } : {
              sx: {
                display: 'none',
              }
            }
          }}
        >
          {options.map(({label, value,  subOptions}, index) => (
            <CustomTab
              key={index}
              value={index}
              options={[
                {label, value: value || 0},
              ...Array.isArray(subOptions) ? 
              subOptions.map(({label, value}, index) => ({
                label, value : value || index + 1
              })) 
              : []
              ]}
              selectd={values.index === index}
            />
          ))}
        </Tabs>
    )
};

GroupButtonTab.defaultProps = {
    options: [
        {
            label: 'a1',
            subOptions : [
                {
                    label: 'mon button a1 de a1',
                },
                {
                    label: 'mon button a2 de a1',
                }
            ]
        },
        {
            label: 'b1',
            refValue: 'mon button b1',
        },
        {
            label: 'c1',
            refValue: 'mon button c1',
        }
    ],
};
