import StarBorderRoundedIcon from '@mui/icons-material/StarBorderRounded';
import {
    Box, 
    CardActionArea, 
    Grid,
    IconButton,
    ImageList,
    ImageListItem,
    ImageListItemBar,
    ListItemButton,
} from '@mui/material';
import { useDispatch, useSelector } from 'react-redux';
import { viewImage } from '../../../../../../redux/apps/medialibrary/medialibraryNavRight';
import useSize from '../../../../../../services/useSize';
// import BookPlan from '../../../../../../../components/BookPlan';
// import handleLocalDateStr from '../../../../../../../utils/handleLocalDateStr';

const srcset = url => ({
      src: `${url}`,
      srcSet: `${url}`,
});

export default function PhotoLibraryContent () {
    const images = useSelector(state => state.medialibraryNavRight.value.data.images);
    const image = useSelector(state => state.medialibraryNavRight
        .value.photoLibrary.selectedImage
    );
    const isMall = useSelector(state => state.medialibraryNavLeft.value.open);
    const dispatch = useDispatch();
    const size = useSize();
    const isSmallScreen = !!['xs', 'md'].find( key  => size.key === key);

    return (
        <Box
            sx={{
                display: 'flex',
                flex: 1,
                overflow: 'hidden',
            }}
        >
            <ImageList
                sx={{
                    transform: 'translateZ(0)',
                    m: 0,
                    p: 1,
                    overflow: 'auto',
                }}
                rowHeight={200}
                cols={ isSmallScreen ? 2 :
                    isMall ? image ? 3 : 4 : image ? 4 : 6}
                gap={1}
            >
                {images.map((item, index) => {
                   
                    return (
                        <CardActionArea
                            key={index}
                            onClick={() => dispatch(viewImage({image: item}))}
                        >
                            <ImageListItem 
                                key={index} 
                                cols={1} 
                                rows={1}
                                sx={{
                                    ...image?._id === item._id ? {
                                        opacity: .6
                                    }: {}
                                }}
                            >
                                <img
                                    {...srcset(item.contentUrl)}
                                    alt={item.title}
                                    loading="lazy"
                                />
                                <ImageListItemBar
                                    sx={{
                                        background:
                                        'linear-gradient(to bottom, rgba(0,0,0,0.7) 0%, ' +
                                        'rgba(0,0,0,0.3) 70%, rgba(0,0,0,0) 100%)',
                                    }}
                                    title={item.title}
                                    position="top"
                                />
                            </ImageListItem>
                        </CardActionArea> 
                    );
                })}
            </ImageList>
        </Box>
    );

}