import {
    Box, 
    Grid, Typography,
} from '@mui/material';
import { Stack } from '@mui/system';
import { useSelector } from 'react-redux';
import BookPlan from '../../../../../../components/BookPlan';
import handleLocalDateStr from '../../../../../../utils/handleLocalDateStr';

const filterData = (books) => {
    return books;
}
export default function BookLibraryContent () {
    const books = useSelector(state => state.medialibraryNavRight.value.data.books);
    const isMall = useSelector(state => state.medialibraryNavLeft.value.open);
    const selectedBookType = useSelector(state => 
        state.medialibraryNavRight.value.bookLibrary.selectedBookType
    );

    const currentBooks = filterData(books?.filter(({type}) =>  
        selectedBookType ? type === selectedBookType : true 
    ));

    return (
        <Box
            sx={{
                display: 'flex',
                flex: 1,
                px: 1,
                mb: 1,
            }}
        >
            {currentBooks.length === 0 ?
                (<Stack
                    display="flex"
                    flex={1}
                    justifyContent="center"
                    alignItems="center"
                >
                    <Typography variant="caption" color="text.secondary">
                        Aucun élement trouvé
                    </Typography>
                </Stack>) :
                <Box>
                <Grid container spacing={1} sx={{pb: 0}}>
                        {currentBooks.map((book, index) => (
                            <Grid
                                item
                                xs={12}
                                md={6}
                                lg={isMall ? 4 : 3}
                                xl={isMall ? 3 : 2}
                                key={index}
                            >
                                <BookPlan 
                                    key={index}
                                    coverUrl={book.coverUrl}
                                    title={book.title}
                                    date={handleLocalDateStr(book.createdAt)}
                                    link={book.contentUrl}
                                />
                            </Grid>
                        ))}
                </Grid>
                </Box>
        }
        </Box>
    );

}
