import * as React from 'react';
import {
    Box, Drawer
} from '@mui/material';
import { useSelector } from 'react-redux';
import MedialibraryNavLeftHeader from './header/MedialibraryLeftHeader';
import MedialibraryNavLeftContent from './content/MedialibraryNavLeftContent';
import useSize from '../../../services/useSize';

export default function MedialibraryNavLeft () {
    const [open, setOpen] = React.useState(false);
    const openNav = useSelector(state => state.medialibraryNavLeft.value.open);
    const size = useSize();
    const isSmallScreen = !!['xs', 'md'].find( key  => size.key === key);

    React.useEffect(() => {
      if(openNav)
        setOpen(true);
    }, [openNav, setOpen]);

    return (
        <Box
        {...isSmallScreen ? {
            component: Drawer,
            open: openNav,
        }: {
            sx:{
                display: 'flex',
                flexDirection: 'column',
                width: openNav ? 360 : 0,
                transition: theme => theme.transitions.create('width', {
                    easing: theme.transitions.easing.sharp,
                    duration: theme.transitions.duration.leavingScreen,
                }),
                overflow: 'hidden',
                bgcolor: 'background.paper',
            },
            onTransitionEnd() {
                if(!openNav)
                    setOpen(false);
            }

        }}
           
        >
            { open &&
                <Box
                    sx={{
                        transition: theme => theme.transitions.create('left', {
                            easing: theme.transitions.easing.sharp,
                            duration: theme.transitions.duration.leavingScreen,
                        }),
                        position: 'relative',
                        left: - openNav ?  0 : -360,
                        display: 'flex',
                        flex: 1,
                        flexDirection: 'column',
                    }}
                >
                    <MedialibraryNavLeftHeader/>
                    <MedialibraryNavLeftContent/>
                </Box>
            }
        </Box>
    )
}