import * as React from 'react';
import {
    Box,
    CardActionArea,
    CardMedia, 
    Chip, 
    ImageListItemBar,
    Skeleton,
    Typography
} from '@mui/material';

const handleTime = times => {
    const moth = Math.floor(times / ( 36000 * 24 * 3));
    const day = Math.floor(((times / ( 36000 * 24 * 3)) - moth) * 30);
    const hours = Math.floor( ((((times / ( 36000 * 24 * 3)) - moth) * 30) - day) * 24 );
    const min = Math.floor( (( ((((times / ( 36000 * 24 * 3)) - moth) * 30) - day) * 24 ) - hours) * 60 );
    const sec = Math.floor( (( (( ((((times / ( 36000 * 24 * 3)) - moth) * 30) - day) * 24 ) - hours) * 60 ) - min) * 60 );
    return `${
        moth ? moth + 'm' : ''
    } ${
        day ? day + 'j' : ''
    } ${
        hours ? hours + ':': ''
    }${
        min.toString().padStart(2, '0')
    }:${
        sec.toString().padStart(2, '0')
    }`.trim();
};

export default function VideoPlan ({src, title, description}) {
    const [data, setData] = React.useState(null);
    
    const handleLoad = event => {
        const duration  = handleTime(event.target.duration);
        setData({duration});
        //console.log(duration);
    };

    return (
        <Box>
            <Box
                sx={{position: 'relative', m: 0, p: 0,}}
            >
                <CardActionArea href={src} target="_blank"> 
                    <CardMedia
                        component="video"
                        src={src}
                        onLoadedMetadata={handleLoad}
                    />
                </CardActionArea>
                {data &&
                <React.Fragment>
                    <Chip
                        label={data.duration}
                        sx={{
                            position: 'absolute',
                            bottom: '5px',
                            right: '5px',
                            bgcolor: 'primary.main',
                            color: 'white'
                        }}
                    />
                </React.Fragment>
                }
            </Box>
            <Box sx={{mx:1, my:0}}>
                <ImageListItemBar
                    title={
                        <Typography
                            variant="h6"
                            fontSize={15}
                            color="text.primary"
                        >{title}</Typography>
                    }
                    subtitle={ data && 
                        <Typography
                            color="text.secondary"
                            variant="caption"
                        >{description}</Typography>
                    }
                    position="below"
                    sx={{
                        maxWidth: {
                            xs: 300, 
                            md: 400, 
                        },
                        whiteSpace: 'nowrap',
                        overflow: 'hiddenn',
                        textOverflow: 'ellipsis',
                    }}
                />
            </Box>
        </Box>
    )
}