
import Carousel from "react-material-ui-carousel";
import {
  Box
} from '@mui/material';
import Banner from "./Banner";
import items from "./items";

export default function Presentation (props) {
 
    return (
      <Box>
        <Carousel
          interval={6000}
          duration={1000}
        >
          {items.map((item, index) => {
            const { content, title } = item;
            const pub = { content, title, type: 'message'};
            return (
              <Banner
                items={
                  (index === 0 && [pub, ...item.items] ) ||
                  (index === 1 && [ item.items[0], pub, item.items[1]] ) ||
                  (index === 2 && [...item.items, pub] )
                }
                key={index}
                contentPosition={item.contentPosition}
              />
            );
          })}
        </Carousel>
      </Box>
    );
}