import { 
    Divider, 
    Stack,
} from "@mui/material";
import MedialibraryNavLeft from "./navleft/MedialibraryNavLeft";
import MedialibraryNavRight from "./navright/MedialibraryNavRight";

import { SnackbarProvider } from "notistack";
import MediaLibraryCover from "./cover/MediaLibraryCover";
import { useSelector } from "react-redux";
import React from "react";

export default function Medialibrary () {
    const open = useSelector(state => state.medialibraryNavRight.value.open);
    return ( 
        <React.Fragment>
           { open &&
            <SnackbarProvider maxSnack={1}>
                <Stack 
                    height="100vh"
                    display="flex"
                    direction="row"
                    overflow="hidden"
                    divider={<Divider orientation="vertical" flexItem/>}
                    bgcolor="background.default"
                >
                    <MedialibraryNavLeft/>
                    <MedialibraryNavRight/>
                </Stack>
            </SnackbarProvider>}
            <MediaLibraryCover loadData={true}/>
        </React.Fragment>
    )
}