import AspectRatioRoundedIcon from '@mui/icons-material/AspectRatioRounded';
import ExitToAppRoundedIcon from '@mui/icons-material/ExitToAppRounded';
import ArrowBackIosRoundedIcon from '@mui/icons-material/ArrowBackIosRounded';
import screenOptions from './screenOptions';
import { store } from '../../../../redux';
import { switchScreen } from '../../../../redux/screen';
import { closeNav } from '../../../../redux/apps/medialibrary/medialibraryNavLeft'; 

const fastOptions = [
    {
      label: 'Ecran',
      icon: AspectRatioRoundedIcon,
      options: screenOptions,
      id: 'screen',
      action (screen) {
        return () => store.dispatch(switchScreen(screen))
      }
    },
    {
      label: 'Quitter', 
      icon: ExitToAppRoundedIcon,
      id: 'quit',
      action (navidateTo) {
        if(typeof navidateTo === 'function')
         return () => {
          const link = document.createElement('a');
          link.href= '/';
          link.click();
         };
      }
    },
    {
      label: 'Reduire',
      icon: ArrowBackIosRoundedIcon,
      id: 'reduis',
      action () {
        return () => store.dispatch(closeNav())
      }
    }
];

export default fastOptions;