import {
    Box
} from '@mui/material';
import { useSelector } from 'react-redux';
import MedialibraryBookLibrary from './booklibrary/MediaLibraryBookLibrary';
import MediaLibraryFilmLibrary from './filmlibrary/MediaLibraryFilmLibrary';
import MedialibraryHomeContent from './home/MedialibraryHomeContent';
import MedialibraryPhotoLibrary from './photolibrary/MedialibraryPhotoLibrary';

export default function MedialibraryNavRightContent () {
    const tab = useSelector(state => state.medialibraryNavRight.value.tab);

    return (
        <Box
            sx={{
                display: 'flex',
                flex: 1,  
                overflow: 'auto',
                flexDirection: 'column',
                height: '100%'
            }}
        >
          {tab === 'home' && <MedialibraryHomeContent />}
          {tab === 'books' && <MedialibraryBookLibrary />}
          {tab === 'images' && <MedialibraryPhotoLibrary />}
          {tab === 'films' && <MediaLibraryFilmLibrary />}
        </Box>
    );
}