
import * as React from 'react'
import {
  Box,
  IconButton,
  ListItemAvatar,
  Tooltip,
  Toolbar,
  AppBar
} from '@mui/material';
import { useSelector } from 'react-redux';
import MenuOptions from '../../../../components/MenuOptions';
import { useNavigate } from 'react-router-dom';
import screenOptions from '../options/screenOptions';
import fastOptions from '../options/fastOptions';

const AppBarStatus = () => {

  const [values, setValues] = React.useState({
    anchorEl: null,
    options: [],
  });

  const navigateTo = useNavigate();
  const screen = useSelector(state => state.screen.value);
  const open = Boolean(values.anchorEl);
  
  const nextState =  screen === 'normal' ? 
  'croped' : screen === 'croped' ? 'full' : 'normal';

  const handleClick = (event, options) => {
    setValues({
      anchorEl: event.currentTarget,
      options,
    });
  };

  return (
    <React.Fragment>
      <AppBar position="sticky" color="default" sx={{boxShadow: 0}}>
        <Toolbar>
          <ListItemAvatar
            sx={{flexGrow: 1, ml: 1}}
          >
          </ListItemAvatar>
          {fastOptions.map((btn, index) => (
            <React.Fragment key={index}>
              <Tooltip 
                title={(() => {
                  const label = btn.id === 'screen' ?
                  screenOptions.find(screen => screen.id === nextState).label :
                  btn.label;
                  return label
                })()} 
                arrow
              >
                <IconButton 
                  size="small" 
                  sx={{ ml: 1 }}
                  onClick={btn.action ? 
                    btn.action([nextState, navigateTo][index]) : 
                    (event => {
                    handleClick(event, btn.options)
                  })}
                >
                {(() => {
                    const Icon = btn.id === 'screen' ?
                     btn.options.find(({id}) => id === nextState).icon :
                     btn.icon;
                    return <Icon fontSize="small" />
                  })()
                }
                </IconButton>
              </Tooltip>
            </React.Fragment>
          ))}
        </Toolbar>
      </AppBar>
      <MenuOptions
        anchorEl={values.anchorEl}
        open={open}
        onClose={() => setValues({
          ...values,
          anchorEl: null,
        })}
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'left',
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'left',
        }}
        options={values.options}
      />
    </React.Fragment>
  )
}

export default function MedialibraryLeftHeader() {

  return (
      <Box>
        <AppBarStatus />
      </Box>
  )
}