import capStr from '../services/capStr';
export default function handleLocalDateStr (
    date, 
    options = { 
        weekday: 'long', 
        year: 'numeric', 
        month: 'long', 
        day: 'numeric',
    }) {

    const stringDate = (new Date (date))
    .toLocaleDateString(options.lang || 'fr', options)
    .toString();

   return stringDate.split(/\s/).map((item, index) => {
    let newItem = null;

    if(isNaN(parseInt(item)))
        newItem =  item.length > 7 ? item.slice(0, 3): item;
    if(!index)
        newItem = capStr(newItem || item);
        return newItem || item
    }).join(' ');
    
}