
import * as React from 'react'
import {
  Box,
  AppBar,
  Toolbar,
  Typography,
  IconButton,
  Tooltip,
} from '@mui/material';
import SettingsRoundedIcon from '@mui/icons-material/SettingsRounded';
import MenuOpenRoundedIcon from '@mui/icons-material/MenuOpenRounded';
import MoreVertRoundedIcon from '@mui/icons-material/MoreVertRounded';
import ExitToAppRoundedIcon from '@mui/icons-material/ExitToAppRounded';
import { useDispatch, useSelector } from 'react-redux';
import { openNav } from '../../../../redux/apps/medialibrary/medialibraryNavLeft';
import { useConfig } from '../../../../services/globalConfig';
import MenuOptions from '../../../../components/MenuOptions';
import { useNavigate } from 'react-router-dom';

const MenuOpenIconHigherOrder = () => {
  const dispatch = useDispatch();
  const isClose = useSelector(state =>  state.medialibraryNavLeft.value.open);
  return ( !isClose &&
      <IconButton
            size="small"
            edge="start"
            onClick={() => dispatch(openNav())}
            sx={{mr:1}}
          >
            <MenuOpenRoundedIcon/>
      </IconButton>
  );
}

const SettingsIconHigherOrder = () => {
  const [ ,actions] = useConfig();

  return ( 
    <Tooltip arrow title="Paramètre" enterDelay={700}>
      <IconButton size="small"
          onClick={() => actions(null).handleChangeProps({settingsNav: true})}
          sx={{mr:1}}
      >
          <SettingsRoundedIcon fontSize="small"/>
      </IconButton>
    </Tooltip>
  );
}

const MoreVertIconHigherOrder = () => {
  const [open, setOpen] = React.useState(false);
  const btnRef = React.useRef();
  //const navidateTo = useNavigate();

  const options = [
    {
      label: 'Quitter',
      icon: ExitToAppRoundedIcon,
      id: 'quit',
      action () {
        const link = document.createElement('a');
        link.href= '/';
        link.click();
      }
    },
  ];

  return ( 
    <React.Fragment>
      <Tooltip arrow title="Plus" enterDelay={700}>
        <IconButton size="small"
            onClick={() => {
              setOpen(true);
            }}
            ref={btnRef}
        >
            <MoreVertRoundedIcon fontSize="small"/>
        </IconButton>
      </Tooltip>
      <MenuOptions
        options={options}
        open={open}
        anchorEl={btnRef.current}
        onClose={() => {
          setOpen(false);
        }}
      />
    </React.Fragment>
  );
}

export default function MedialibraryRightHeader() {
  

  return (
      <React.Fragment>
       <AppBar position="sticky" color="default">
         <Toolbar>
            <MenuOpenIconHigherOrder/>
            <Typography sx={{flexGrow: 1}}>
              Mediathèque
            </Typography>
           <SettingsIconHigherOrder/>
           <MoreVertIconHigherOrder/>
         </Toolbar>
       </AppBar>
      </React.Fragment>
  )
}