import * as React from "react";
import { 
    List, 
    ListItem, 
    ListItemButton, 
    ListItemText, 
    ListSubheader, 
    Box,
    CircularProgress,
    Backdrop, ImageListTile, ListItemIcon
} from "@mui/material";
import { useDispatch, useSelector } from "react-redux";

import bookImage from '../../../../icons/icons8-book-shelf-64.png';
import photoImage from '../../../../icons/icons8-photo-gallery-64.png';
import filmImage from '../../../../icons/icons8-video-gallery-64.png';
import ImgToIcon from '../../../../icons/ImgToIcon';
import { setTab } from "../../../../redux/apps/medialibrary/medialibraryNavRight";
import useSize from "../../../../services/useSize";
import { closeNav } from "../../../../redux/apps/medialibrary/medialibraryNavLeft";



export default function MedialibraryNavLeftContent () {
    const selectedTab = useSelector(state => state.medialibraryNavRight.value.tab);
    const dispatch = useDispatch();
    const size = useSize();
    const isSmallScreen = !!['xs', 'md'].find(key => size.key === key);
    const options = [
        {
            label: 'Bibiothèque',
            icon: bookImage,
            id: 'books'
        },
        {
            label: 'Photothèque',
            icon: photoImage,
            id: 'images',
        },
        {
            label: 'Filmothèque',
            icon: filmImage,
            id: 'films',
        },
    ];

    return (
        <React.Fragment>
            <List
            sx={{
                width: '100%',
                position: 'relative',
                overflow: 'auto',
                mt: 1,
            }}
            disablePadding
            >
            {options.map((tab, index) => (
                <ListItem 
                    key={`${index}`} 
                    sx={{m:0, p: 0, fontSize: 15}}
                    selected={tab.id === selectedTab}
                    onClick={() => {
                        dispatch(setTab({tab: tab.id}));
                        if(isSmallScreen)
                            dispatch(closeNav());
                    }}
                >
                    <ListItemButton>
                        <ListItemIcon>
                            <ImgToIcon
                                src={tab.icon}
                                size={30}
                            />
                        </ListItemIcon>
                        <ListItemText 
                            primary={`${tab.label}`} 
                            primaryTypographyProps={{
                                color:'text.primary'
                            }}
                        />
                    </ListItemButton>
                </ListItem>
                ))
                }
            </List>
        </React.Fragment>
    );
}